html, body, #root, .App {
  height: 100%;
}

.flex {
  display: flex;
  flex-direction: column;
}

.App {
  align-items: center;
}

.navbar {
  background-color: blue;
  height: 30px;
  width: 100%;
}

.container {
  text-align: center;
  width: 100%;
  max-width: 800px;
  flex: 1;
}

.top {
  flex-direction: row;
  flex-wrap: wrap-reverse;
  align-items: stretch;
}

.controls {
  background-color: yellow;
  justify-content: space-between;
  flex: 1;
  min-width: 300px;
}

.ballance {
  background-color: blue;
  height: 30px;
}

.filter {
  background-color: red;
  height: 30px;
}

.form {
  background-color: green;
  flex: 1;
  min-width: 300px;
}

.formControls {
  height: 200px;
}

.transactions {
  flex-grow: 1;
}

.transactionItem {
  height: 40px;
}

.remove {
  cursor: pointer;
}

.scroll {
  height: 100%;
}

.footer {
  background-color: pink;
  height: 30px;
  width: 100%;
}
